import React from "react"
import styled from "styled-components"

const Container = styled.div`
  margin: 5px auto 20px;
`

const Post = ({ title, children, className = ["is-dark"] }) => {
  return (
    <Container>
      <div
        className={`nes-container ${title && "with-title"} ${className.join(
          " "
        )}`}
      >
        <p className="title">{title}</p>
        {children}
      </div>
    </Container>
  )
}

export default Post
